.account-deletion{max-width:600px;margin:0 auto;padding:40px 20px;background:#fff;min-height:100vh}.deletion-content{text-align:center}.deletion-title{font-size:28px;font-weight:700;color:#1a1a1a;margin-bottom:16px}.deletion-description{font-size:16px;color:#666;margin-bottom:32px;line-height:1.5}.warning-box{background:#fff3cd;border:1px solid #ffeaa7;border-radius:8px;padding:20px;margin-bottom:32px;text-align:left}.warning-box h3{font-size:18px;font-weight:600;color:#856404;margin-bottom:12px}.warning-box ul{list-style:none;padding:0;margin:0}.warning-box li{font-size:14px;color:#856404;margin-bottom:8px;padding-left:20px;position:relative}.warning-box li::before{content:"•";position:absolute;left:0;color:#856404;font-weight:bold}.delete-button{background:#dc3545;color:#fff;border:none;border-radius:8px;padding:16px 32px;font-size:16px;font-weight:600;cursor:pointer;transition:background-color .2s}.delete-button:hover{background:#c82333}.delete-button:active{transform:translateY(1px)}.modal-overlay{position:fixed;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,.5);display:flex;align-items:center;justify-content:center;z-index:1000}.modal-content{background:#fff;border-radius:12px;padding:32px;max-width:400px;width:90%;box-shadow:0 10px 25px rgba(0,0,0,.2)}.modal-title{font-size:20px;font-weight:700;color:#1a1a1a;margin-bottom:12px;text-align:center}.modal-description{font-size:14px;color:#666;margin-bottom:24px;text-align:center;line-height:1.5}.password-input{margin-bottom:20px}.password-input label{display:block;font-size:14px;font-weight:600;color:#333;margin-bottom:8px}.password-input input{width:100%;padding:12px 16px;border:1px solid #ddd;border-radius:6px;font-size:14px;transition:border-color .2s}.password-input input:focus{outline:none;border-color:#007aff}.password-input input:disabled{background:#f5f5f5;cursor:not-allowed}.error-message{background:#f8d7da;color:#721c24;padding:12px;border-radius:6px;font-size:14px;margin-bottom:20px;text-align:center}.modal-buttons{display:flex;gap:12px}.cancel-btn{flex:1;padding:12px 24px;background:#fff;color:#666;border:1px solid #ddd;border-radius:6px;font-size:14px;font-weight:600;cursor:pointer;transition:all .2s}.cancel-btn:hover{background:#f8f9fa;border-color:#ccc}.cancel-btn:disabled{opacity:.6;cursor:not-allowed}.confirm-btn{flex:1;padding:12px 24px;background:#dc3545;color:#fff;border:none;border-radius:6px;font-size:14px;font-weight:600;cursor:pointer;transition:background-color .2s}.confirm-btn:hover:not(:disabled){background:#c82333}.confirm-btn:disabled{background:#ccc;cursor:not-allowed}@media(max-width: 768px){.account-deletion{padding:20px 16px}.deletion-title{font-size:24px}.modal-content{padding:24px}.modal-buttons{flex-direction:column}.cancel-btn,.confirm-btn{flex:none}}