/* 로그인 페이지 스타일 */
.login-wrap {
  min-height: 100vh;
  background: #fff;
  position: relative;
}

/* 헤더 */
.login-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 40px 20px;
  background: #fff;
  position: sticky;
  top: 0;
  z-index: 100;
}

.icon-btn {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: background-color 0.2s;
  
  &:hover {
    background: #f8f9fa;
  }
}

.login-title {
  font-size: 32px;
  font-weight: 700;
  color: #333;
  margin: 0;
  text-align: center;
}

/* 메인 콘텐츠 */
.login-main {
  padding: 40px 20px;
  max-width: 1400px;
  margin: 0 auto;
  width: 100%;
}

.login-desc {
  text-align: center;
  color: #666;
  font-size: 14px;
  line-height: 1.5;
  margin: 0 0 32px;
}

/* 소셜 로그인 영역 */
.social-login-area {
  margin-bottom: 24px;
}

.social-btn {
  cursor: pointer;
  transition: opacity 0.2s;
  
  &:hover {
    opacity: 0.9;
  }
}

/* 구분선 */
.divider {
  text-align: center;
  margin: 24px 0;
  position: relative;
  
  &::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    height: 1px;
    background: #e9ecef;
  }
  
  span {
    background: #f8f9fa;
    padding: 0 16px;
    color: #666;
    font-size: 14px;
    position: relative;
  }
}

/* 탭 영역 */
.tab-area {
  display: flex;
  margin-bottom: 24px;
  border-bottom: 1px solid #e9ecef;
}

.tab-btn {
  flex: 1;
  padding: 12px 0;
  background: none;
  border: none;
  font-size: 14px;
  color: #666;
  cursor: pointer;
  border-bottom: 2px solid transparent;
  transition: all 0.2s;
  
  &.active {
    color: #333;
    border-bottom-color: #007bff;
    font-weight: 600;
  }
  
  &:hover {
    color: #333;
  }
}

/* 로그인 폼 */
.login-form {
  margin-bottom: 24px;
}

.input-group {
  margin-bottom: 20px;
}

.input-label {
  display: block;
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin-bottom: 8px;
}

.input {
  width: 100%;
  padding: 20px 24px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 18px;
  transition: border-color 0.2s;
  
  &:focus {
    outline: none;
    border-color: #007bff;
  }
  
  &::placeholder {
    color: #999;
  }
}

/* 보안 접속 스위치 */
.secure-row {
  margin-bottom: 24px;
}

.secure-switch {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-size: 14px;
  color: #666;
  
  input[type="checkbox"] {
    display: none;
  }
  
  .slider {
    width: 40px;
    height: 20px;
    background: #ddd;
    border-radius: 20px;
    position: relative;
    margin-right: 8px;
    transition: background-color 0.2s;
    
    &::before {
      content: '';
      position: absolute;
      top: 2px;
      left: 2px;
      width: 16px;
      height: 16px;
      background: #fff;
      border-radius: 50%;
      transition: transform 0.2s;
    }
  }
  
  input[type="checkbox"]:checked + .slider {
    background: #007bff;
    
    &::before {
      transform: translateX(20px);
    }
  }
}

.label-text {
  font-size: 14px;
  color: #666;
}

/* 로그인 버튼 */
.login-btn {
  width: 100%;
  padding: 18px 24px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
  
  &:hover {
    background: #0056b3;
  }
  
  &:active {
    transform: translateY(1px);
  }
}

/* 로그인 링크 */
.login-links {
  text-align: center;
  font-size: 16px;
  padding: 16px 0;
}

.link {
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  text-decoration: none;
  transition: color 0.2s;
  padding: 8px 16px;
  margin: 0 4px;
  border-radius: 4px;
  
  &:hover {
    color: #333;
    background: #f8f9fa;
  }
  
  &.strong {
    color: #007bff;
    font-weight: 600;
    
    &:hover {
      color: #0056b3;
      background: #e3f2fd;
    }
  }
}

.divider-dot {
  color: #ddd;
  margin: 0 8px;
}

/* 소셜 로그인 모달 */
#socialLoginLayer {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
}

.layer-content {
  background: #fff;
  border-radius: 12px;
  padding: 24px;
  max-width: 400px;
  width: 90%;
  position: relative;
  text-align: center;
}

.close-btn {
  position: absolute;
  top: 12px;
  right: 12px;
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
  
  &:hover {
    color: #333;
  }
}

.social-mock-logo {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  font-weight: bold;
  color: #fff;
  margin: 0 auto 16px;
  
  &.kakao {
    background: #fee500;
    color: #3c1e1e;
  }
  
  &.naver {
    background: #03c75a;
  }
}

.social-mock-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin-bottom: 24px;
}

.social-mock-btn {
  width: 90%;
  padding: 12px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
  
  &:hover {
    background: #0056b3;
  }
}

/* 반응형 */
@media (max-width: 768px) {
  .login-main {
    padding: 20px 16px;
    max-width: 100%;
  }
  
  .login-title {
    font-size: 28px;
  }
  
  .input {
    padding: 14px 16px;
    font-size: 16px;
  }
  
  .login-btn {
    padding: 16px;
    font-size: 16px;
  }
  
  .social-btn img {
    width: 120px !important;
    height: 40px !important;
  }
}

@media (max-width: 480px) {
  .login-main {
    padding: 16px 12px;
  }
  
  .login-title {
    font-size: 24px;
  }
  
  .social-btn img {
    width: 100px !important;
    height: 35px !important;
  }
}
