e:14px;color:#1a1a1a;font-weight:500;margin-bottom:0;margin-left:auto}.required-notice .dot{color:red;font-size:16px;margin-right:4px}.form-section-title-row{display:flex;align-items:center;justify-content:space-between;margin-bottom:16px}.form-input-group{flex:1;max-width:calc(100% - 136px);position:relative}.form-input{width:100%;height:44px;border:1px solid #e5e5e5;border-radius:6px;padding:0 44px 0 12px;font-size:14px;color:#1a1a1a;background-color:#fff;transition:border-color .2s;box-sizing:border-box;overflow:hidden}#password,#confirmPassword{padding-right:44px !important}.pw-toggle-btn{position:absolute !important;top:50% !important;right:12px !important;transform:translateY(-50%) !important;background:none !important;border:none !important;padding:0 !important;cursor:pointer !important;z-index:10 !important;display:flex !important;align-items:center !important;justify-content:center !important;height:24px !important;width:24px !important;pointer-events:auto !important}.pw-toggle-btn svg{width:20px !important;height:20px !important;flex-shrink:0 !important}.form-input:focus{outline:none;border-color:#007aff}.form-input.error{border-color:#1a1a1a}.form-hint{font-size:12px;color:#999;margin-top:4px}.radio-group{display:flex;gap:16px;padding-top:12px}.radio-item{display:flex;align-items:center;gap:8px;cursor:pointer}.radio-input{width:16px;height:16px;accent-color:#007aff}.radio-label{font-size:14px;color:#1a1a1a}.address-group{display:flex;flex-direction:column;gap:8px;width:100%}.address-row{display:flex;gap:8px;align-items:center}.address-input{width:100%;height:44px;border:1px solid #e5e5e5;border-radius:6px;padding:0 12px;font-size:14px;color:#1a1a1a;background-color:#fff;box-sizing:border-box}.address-search-btn{height:44px;padding:0 16px;background-color:#fff;border:1px solid #e5e5e5;border-radius:6px;font-size:14px;color:#1a1a1a;cursor:pointer;transition:all .2s;white-space:nowrap}.address-search-btn:hover{background-color:#f8f9fa}.phone-group{display:flex;gap:8px;align-items:center}.phone-select{width:120px;min-width:100px;height:44px;border:1px solid #e5e5e5;border-radius:6px;padding:0 8px;font-size:14px;color:#1a1a1a;background-color:#fff;box-sizing:border-box}.phone-input{flex:1;height:44px;border:1px solid #e5e5e5;border-radius:6px;padding:0 12px;font-size:14px;color:#1a1a1a;text-align:center;background-color:#fff;box-sizing:border-box}.phone-separator{font-size:14px;color:#999}.button-section{padding:20px 16px;background-color:#fff}.button-group{display:flex;gap:12px}.cancel-btn{flex:1;height:44px;background-color:#fff;color:#666;border:1px solid #e5e5e5;border-radius:6px;font-size:15px;font-weight:600;cursor:pointer;transition:all .2s ease}.cancel-btn:hover{background-color:#f8f9fa;border-color:#ccc}.register-btn{flex:1;height:44px;background-color:#1a1a1a;color:#fff;border:none;border-radius:6px;font-size:15px;font-weight:600;cursor:pointer;transition:all .2s ease}.register-btn:hover{background-color:#333}.register-btn:disabled{background-color:#e5e5e5;color:#999;cursor:not-allowed}#userId,.phone-input,.phone-select{border-color:#1a1a1a}@media(max-width: 720px){.container{max-width:100%}.form-row{flex-direction:column;gap:8px}.form-label{min-width:auto;padding-top:0}.phone-group{flex-wrap:wrap}.phone-select{width:100%}}#postcodeLayer{display:none;position:fixed;z-index:1000;left:0;top:0;right:0;bottom:0;background:rgba(0,0,0,.3)}#postcodeLayer .layer-content{position:absolute;left:50%;top:50%;transform:translate(-50%, -50%);background:#fff;border-radius:8px;box-shadow:0 4px 24px rgba(0,0,0,.18);padding:16px;width:100%;max-width:500px;min-width:320px}#postcodeLayerContent{width:100%;height:400px}#postcodeLayer .close-btn{display:block;margin:0 0 8px auto;background:none;border:none;font-size:18px;cursor:pointer}