:root{--bg:#fff;--text:#111;--muted:#777;--line:rgba(0,0,0,.08);--pill:#f3f4f6;--accent:#111;--container:1200px}*{box-sizing:border-box}body{margin:0;color:var(--text);font:14px/1.5 system-ui,-apple-system,"Segoe UI",Roboto,Arial}.container{max-width:var(--container);margin:0 auto;padding:0 20px}.site-header{background:var(--bg);border-bottom:1px solid var(--line);position:sticky;top:0;z-index:50}.header-inner{height:64px;display:grid;align-items:center;gap:10px;grid-template-columns:auto 1fr auto;grid-template-areas:"logo nav actions";max-width:var(--container);margin:0 auto;padding:0 20px}.logo{grid-area:logo;justify-self:start;display:inline-flex;align-items:center;text-decoration:none;color:var(--text);font-weight:800}.logo-img{height:40px;width:auto;display:block}.logo-text h1{margin:0;font-size:24px;font-weight:800;color:var(--text);line-height:1.2}.nav{grid-area:nav;display:flex;justify-content:flex-start;margin-left:20px}.nav-list{display:flex;align-items:center;justify-content:flex-start;gap:24px;padding:0;margin:0;list-style:none;white-space:nowrap}.nav-list>li{position:relative}.nav-list>li.category{display:flex;align-items:center;gap:8px;padding-right:12px;margin-right:12px}.nav-list>li.category::after{content:"";position:absolute;right:0;top:50%;transform:translateY(-50%);width:1px;height:16px;background:rgba(0,0,0,.22)}.cat-btn{display:flex;align-items:center;gap:8px;padding:8px 12px;border:0;background:rgba(0,0,0,0);cursor:pointer;border-radius:6px;transition:background-color .2s;font-family:system-ui,-apple-system,"Segoe UI",Roboto,Arial,sans-serif !important}.cat-btn:hover{background:var(--pill)}.cat-btn img{width:20px;height:20px;display:block;object-fit:contain;filter:brightness(0)}.cat-btn span{font-size:14px;font-weight:600;color:var(--text);font-family:system-ui,-apple-system,"Segoe UI",Roboto,Arial,sans-serif !important}.nav a{text-decoration:none;color:var(--text);font-weight:600;opacity:.9;cursor:pointer;font-family:system-ui,-apple-system,"Segoe UI",Roboto,Arial,sans-serif !important}.nav a.active{opacity:1}.nav a:hover{opacity:1;text-decoration:underline}.actions{grid-area:actions;justify-self:end;display:flex;align-items:center;gap:10px}.search-wrap{position:relative}.search{display:flex;align-items:center;gap:8px;padding:6px 10px 6px 12px;background:#e6e6e6;border-radius:999px;border:1px solid rgba(0,0,0,0)}.search:focus-within{border-color:#ddd;background:#fff}.search input{width:210px;border:0;outline:0;background:rgba(0,0,0,0);font-size:13px}.search .btn-search{width:30px;height:30px;border:0;border-radius:999px;background:#e9ecef;cursor:pointer;display:grid;place-items:center}.icon-btn{display:grid;place-items:center;width:36px;height:36px;border:1px solid var(--line);border-radius:10px;text-decoration:none;color:var(--text);background:#fff;position:relative;transition:box-shadow .15s ease}.icon-btn:hover{box-shadow:0 4px 8px rgba(0,0,0,.15)}.icon-btn:hover .icon-img{filter:brightness(1);transform:scale(1.1)}.icon-img{width:20px;height:20px;display:block;object-fit:contain;filter:brightness(0.7);transition:all .15s ease}.icon-btn img[alt=장바구니]{width:22px;height:22px}.cart-badge{position:absolute;top:-8px;right:-8px;background:#ff4757;color:#fff;border-radius:50%;width:22px;height:22px;font-size:11px;display:flex;align-items:center;justify-content:center;font-weight:bold;border:2px solid #fff;box-shadow:0 2px 4px rgba(0,0,0,.2)}.search-panel{position:absolute;right:-36px;top:calc(100% + 11px);width:360px;background:#fff;border:1px solid #e6e6e6;border-radius:12px;box-shadow:0 8px 30px rgba(3,5,4,.17);padding:16px;z-index:30}.search-panel .banner{height:96px;background:#ddd;border-radius:8px;display:flex;align-items:center;justify-content:center;margin-bottom:12px;font-size:14px;color:#333}.search-panel h4{margin:0 0 10px;font-size:16px;font-weight:600}.search-panel .keywords{display:grid;grid-template-columns:1fr 1fr;gap:8px}.search-panel ul{list-style:none;margin:0;padding:0}.search-panel li a{display:block;padding:4px 0;font-size:14px;color:var(--text);text-decoration:none;cursor:pointer}.search-panel li a:hover{text-decoration:underline}.category-panel{overflow:hidden;max-height:0;opacity:0;transform:translateY(-8px);transition:max-height .35s ease,opacity .25s ease,transform .35s ease,padding .2s ease;background:#fff;border-bottom:1px solid var(--line);padding:0}.category-panel.open{max-height:520px;opacity:1;transform:translateY(0);padding:18px 0 22px}.category-grid{display:grid;grid-template-columns:160px repeat(6, 1fr);gap:28px;align-items:start;position:relative}.category-grid::before,.category-grid::after{content:"";position:absolute;top:0;bottom:0;width:1px;background:var(--line)}.category-grid::before{left:160px}.category-grid::after{right:0}.category-col h5{margin:0 0 10px;font-size:14px;font-weight:600}.category-col h5 a{color:var(--text);text-decoration:none;cursor:pointer}.category-col h5 a:hover{text-decoration:underline}.category-col ul{list-style:none;padding:0;margin:0;display:grid;grid-template-rows:repeat(var(--sub-count, 12), auto);row-gap:6px}.category-col li a{display:block;font-size:13px;color:var(--text);text-decoration:none;cursor:pointer;line-height:1.6}.category-col li a:hover{text-decoration:underline}.category-col.title{padding-top:4px;font-size:14px;color:var(--muted)}@media(max-width: 1200px){.header-inner{padding:0 15px}.nav-list{gap:18px}.search input{width:180px}.category-grid{gap:22px}}@media(max-width: 992px){.header-inner{padding:0 12px;gap:8px}.nav-list{gap:16px}.search input{width:160px}.nav{margin-left:15px}}@media(max-width: 768px){.header-inner{padding:0 10px;gap:6px}.nav-list{gap:12px}.search input{width:140px}.nav{margin-left:10px}.logo-text h1{font-size:20px}}@media(max-width: 576px){.header-inner{padding:0 8px;gap:4px}.nav-list{gap:8px}.search input{width:120px}.nav{margin-left:8px}.logo-text h1{font-size:18px}.cat-btn{padding:6px 8px}.cat-btn span{font-size:13px}}