/* 회원가입 페이지 스타일 */
.signup-wrap {
  min-height: 100vh;
  background: #f8f9fa;
  position: relative;
}

/* 헤더 */
.signup-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px 20px;
  background: #fff;
  border-bottom: 1px solid #e9ecef;
  position: sticky;
  top: 0;
  z-index: 100;
}

.icon-btn {
  background: none;
  border: none;
  padding: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  transition: background-color 0.2s;
  
  &:hover {
    background: #f8f9fa;
  }
}

.signup-title {
  font-size: 18px;
  font-weight: 600;
  color: #333;
  margin: 0;
}

/* 메인 콘텐츠 */
.signup-main {
  padding: 24px 20px;
  max-width: 480px;
  margin: 0 auto;
}

/* 회원가입 폼 */
.signup-form {
  margin-bottom: 24px;
}

.input-group {
  margin-bottom: 20px;
}

.input-label {
  display: block;
  font-size: 14px;
  font-weight: 600;
  color: #333;
  margin-bottom: 8px;
}

.input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 16px;
  transition: border-color 0.2s;
  
  &:focus {
    outline: none;
    border-color: #007bff;
  }
  
  &::placeholder {
    color: #999;
  }
}

/* 약관 동의 섹션 */
.agreement-section {
  margin: 32px 0;
  padding: 20px;
  background: #f8f9fa;
  border-radius: 8px;
}

.agreement-title {
  font-size: 16px;
  font-weight: 600;
  color: #333;
  margin: 0 0 16px;
}

.agreement-item {
  display: flex;
  align-items: center;
  margin-bottom: 12px;
  cursor: pointer;
  
  input[type="checkbox"] {
    display: none;
  }
  
  .checkmark {
    width: 20px;
    height: 20px;
    border: 2px solid #ddd;
    border-radius: 4px;
    margin-right: 12px;
    position: relative;
    transition: all 0.2s;
    
    &::after {
      content: '';
      position: absolute;
      left: 6px;
      top: 2px;
      width: 6px;
      height: 10px;
      border: solid #fff;
      border-width: 0 2px 2px 0;
      transform: rotate(45deg);
      opacity: 0;
      transition: opacity 0.2s;
    }
  }
  
  input[type="checkbox"]:checked + .checkmark {
    background: #007bff;
    border-color: #007bff;
    
    &::after {
      opacity: 1;
    }
  }
}

.agreement-text {
  font-size: 14px;
  color: #666;
  line-height: 1.4;
  
  strong {
    color: #333;
  }
}

/* 회원가입 버튼 */
.signup-btn {
  width: 100%;
  padding: 14px;
  background: #007bff;
  color: #fff;
  border: none;
  border-radius: 8px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
  
  &:hover {
    background: #0056b3;
  }
  
  &:active {
    transform: translateY(1px);
  }
}

/* 회원가입 링크 */
.signup-links {
  text-align: center;
  font-size: 14px;
  color: #666;
}

.link {
  background: none;
  border: none;
  color: #007bff;
  cursor: pointer;
  text-decoration: none;
  font-weight: 600;
  margin-left: 8px;
  transition: color 0.2s;
  
  &:hover {
    color: #0056b3;
  }
}

/* 반응형 */
@media (max-width: 480px) {
  .signup-main {
    padding: 20px 16px;
  }
  
  .agreement-section {
    padding: 16px;
  }
}
