:root { 
  --line: rgba(0,0,0,.08); 
  --muted: #777; 
  --text: #111; 
  --red: #e60023; 
}

.product-grid2 {
  .subheading2 { 
    font-size: 14px; 
    color: var(--muted); 
    margin-bottom: 6px; 
  }
  
  .title2 { 
    font-size: 22px; 
    font-weight: 700; 
    margin-bottom: 22px; 
  }
}

.grid2 { 
  display: grid; 
  grid-template-columns: repeat(4, 1fr); 
  gap: 4px; 
  margin-bottom: 30px;
  justify-items: center;
}

.card2 {
  border: none; 
  border-radius: 10px; 
  overflow: hidden; 
  background: #fff;
  transition: transform .15s, box-shadow .15s;
  position: relative;
  height: 400px;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 300px;
  
  &:hover { 
    transform: translateY(-3px); 
    box-shadow: 0 10px 20px rgba(0,0,0,.1); 
  }

  .thumb2 { 
    aspect-ratio: 3/2; 
    background: #f7f7f7; 
    display: flex; 
    align-items: center; 
    justify-content: center;
    height: 220px;
  }
  
  .thumb2 img { 
    width: 90%; 
    height: 90%; 
    object-fit: contain; 
    display: block; 
  }

  .meta2 { 
    padding: 16px 16px 20px; 
    color: var(--text); 
    flex: 1;
    display: flex;
    flex-direction: column;
    position: relative;
  }

  .price2 { 
    display: flex; 
    gap: 8px; 
    align-items: baseline; 
    margin-bottom: 12px;
    
    .sale2 { 
      color: #e60023; 
      font-weight: 500; 
      font-size: 14px; 
    }
    
    .now2 { 
      color: #000;
      font-size: 20px; 
      font-weight: 700;
    }
    
    .base2 { 
      color: #999; 
      font-size: 14px; 
      text-decoration: line-through; 
    }
  }

  .name2 { 
    font-size: 15px; 
    margin-bottom: 8px; 
    line-height: 1.4;
    color: #333;
  }

  .badges2 { 
    display: flex; 
    gap: 6px; 
    flex-wrap: wrap; 
    margin-bottom: 8px;
    padding-top: 8px;
    border-top: 1px solid #ddd;
    
    .badge2 { 
      font-size: 11px; 
      padding: 2px 6px; 
      border-radius: 999px; 
      color: #555; 
      background: #fff; 
    }
  }

  .rating-reviews2 { 
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 12px; 
    color: var(--muted); 
    margin: 0 0 8px;
    
    .rating2 {
      color: #999;
    }
    
    .divider2 {
      color: #ddd;
    }
    
    .reviews2 {
      color: #666;
    }
  }
  
  .divider2 {
    height: 1px;
    background: #eee;
    margin: 8px 0;
  }

  .actions2 {
    display: flex; 
    gap: 8px; 
    justify-content: flex-end;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
    position: absolute;
    bottom: 12px;
    right: 12px;

    .icon-btn2 {
      width: 36px; 
      height: 36px; 
      border-radius: 50%;
      border: 1px solid #ddd; 
      background: #fff; 
      color: #333;
      display: flex; 
      align-items: center; 
      justify-content: center;
      transition: transform .1s ease, box-shadow .15s ease, background .15s ease, color .15s ease;
      cursor: pointer;
      flex-shrink: 0;

      img { 
        display: block; 
        width: 20px;
        height: 20px;
        object-fit: contain;
        filter: brightness(0.7);
        transition: all .15s ease;
      }

      &:hover { 
        transform: translateY(-1px); 
        box-shadow: 0 4px 12px rgba(0,0,0,.15); 
        
        img {
          filter: brightness(1);
          transform: scale(1.1);
        }
      }
    }

    /* 하트 상태에 따른 스타일 */
    .heart2 {
      position: relative;
      overflow: hidden;
      
      img {
        transition: all .15s ease;
      }
      
      &.liked {
        img {
          opacity: 1;
          filter: brightness(1) saturate(1.2);
        }
      }
      
      &:not(.liked) {
        img {
          opacity: 0.7;
        }
      }
      
      &:hover {
        transform: scale(1.1);
        box-shadow: 0 2px 8px rgba(0,0,0,0.15);
        
        img {
          filter: brightness(1);
          opacity: 0;
        }
        
        &::after {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 20px;
          height: 20px;
          background-image: url('../../public/icons/heart_full.png');
          background-size: contain;
          background-repeat: no-repeat;
          background-position: center;
          transform: translate(-50%, -50%);
          z-index: 1;
        }
        
        &::before {
          content: '';
          position: absolute;
          top: 50%;
          left: 50%;
          width: 0;
          height: 0;
          background: rgba(255, 107, 107, 0.3);
          border-radius: 50%;
          transform: translate(-50%, -50%);
          animation: heartPulse2 0.6s ease-out;
        }
      }
      
      &:active {
        img {
          transform: scale(0.95);
        }
      }
    }
    
    /* 장바구니 버튼 특별 스타일 */
    .cart2 {
      &:hover {
        img {
          filter: brightness(1) saturate(1.1);
          transform: scale(1.05);
        }
      }
    }
  }
}

/* 애니메이션 */
@keyframes heartPulse2 {
  0% {
    width: 0;
    height: 0;
    opacity: 1;
  }
  100% {
    width: 40px;
    height: 40px;
    opacity: 0;
  }
}

.pagination2 {
  display: flex; 
  justify-content: center; 
  gap: 6px; 
  margin: 28px 0;
  
  button { 
    border: 1px solid var(--line); 
    background: #fff; 
    padding: 6px 10px; 
    border-radius: 4px; 
    cursor: pointer; 
    transition: background .15s ease;
  }
  
  .active { 
    background: #111; 
    color: #fff; 
    font-weight: 700; 
  }
  
  button:hover { 
    background: #f3f3f3; 
  }
  
  button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
}

/* 반응형 디자인 */
@media (max-width: 768px) {
  .grid2 {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
    gap: 16px;
  }
  
  .card2 {
    .meta2 {
      padding: 8px 10px 12px;
    }
    
    .name2 {
      font-size: 12px;
    }
    
    .actions2 {
      .icon-btn2 {
        width: 30px;
        height: 30px;
      }
    }
  }
}

@media (max-width: 480px) {
  .grid2 {
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 12px;
  }
}